import React, { useState, useEffect } from 'react'
import { Form, Container } from "react-bootstrap";
import personService from './services/persons'
import './index.css'

// Component for filtering the phonebook. Props:
// filterStr: string to filter the phonebook names
// handleChange: handler for textbox onChange
const Filter = (props) => {
  return (
    <div>
      Filter shown with <input value={props.filterStr} onChange={props.handleChange} />
    </div>
  )
}

// Component for new phonebook entry form. Props:
// onSubmit: form onSubmit handler
// newName: state for the name textbox
// handleNameChange: handler for name textbox onChange
// newNumber: state for number textbox
// handleNumberChange: handler for the number textbox
const PersonForm = (props) => {
  return (
    <Form onSubmit={props.onSubmit}>
      <Form.Group controlId="formNewName">
        <Form.Label>Name</Form.Label>
        <Form.Control 
          type="text"
          value={props.newName} 
          onChange={props.handleNameChange} 
          name="newName" />
      </Form.Group>
      <Form.Group controlId="formNewNumber">
        <Form.Label>Number</Form.Label>
        <Form.Control 
          type="text"
          value={props.newNumber} 
          onChange={props.handleNumberChange} 
          name="newNumber"
        />
      </Form.Group>
      
      <Form.Group>
        <button type="submit">add</button>
      </Form.Group>
    </Form>
  )
}

const Persons = ({persons, filter, handleRemovePerson}) => {
  return (
    <div>
      {persons.filter(person => person.name.toLowerCase().includes(filter.toLowerCase()))
        .map(person => 
        <p key={person.id}>
          {person.name} {person.number}
          <button onClick={() => handleRemovePerson(person.id)}>Delete</button>
        </p>
      )}
    </div>
  )
}

const Notification = ({ message, type }) => {
  if (message === null) {
    return null
  }

  return (
    <div className={type}>
      {message}
    </div>
  )
}

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filter, setFilter ] = useState('')
  const [ notification, setNotification ] = useState(null)
  const [ notificationType, setNotificationType ] = useState('')

  useEffect(() => {
    console.log('effect')

    personService
      .getAll()
      .then(initialPersons => {
        setPersons(initialPersons)
      })
  }, [])

  const handleNameChange = (event) => {
    console.log(event.target.value)
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    console.log(event.target.value)
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    console.log(event.target.value)
    setFilter(event.target.value)
  }
  
  const timeoutNotification = () => {
    setTimeout(() => {
      setNotification(null)
    }, 5000)
  }

  const addName = (event) => {
    event.preventDefault()
    console.log(persons)
    const findPerson = persons.find(person => person.name === newName)

    // check if the person is already in the phonebook
    if (findPerson) {
      //alert(`${newName} already exists`)
      const confirm = window.confirm(`${findPerson.name} is already added to the phonebook. Replace the old number with a new one?`)
      
      if (confirm) {
        const newPerson = {name: newName, number: newNumber}
        personService
          .update(findPerson.id, newPerson)
          .then(returnedPerson => {
            setPersons(persons.map(person => person.id !== findPerson.id ? person : returnedPerson))
            resetAfterAdd(newName)
          })
          .catch(err => {
            //setNotification(`Information of ${newName} has already been removed from server`)
            setNotification(`Error: ${err.response.data.error}`)
            setNotificationType('error')
            console.log(err.response.data)
          })
      }

    } else {
      const newPerson = {name: newName, number: newNumber}
      personService
        .create(newPerson)
        .then(returnedPerson => {
          setPersons(persons.concat(returnedPerson))
          resetAfterAdd(newName)
        })
        .catch(err => {
          //setNotification(`Information of ${newName} has already been removed from server`)
          setNotification(`Error: ${err.response.data.error}`)
          setNotificationType('error')
          console.log(err.response.data)
        })
    }
        
  }

  const resetAfterAdd = (newName) => {
    setNewName('')
    setNewNumber('')
    setNotification(`Added ${newName}`)
    setNotificationType('success')
    timeoutNotification()
  }

  const removePerson = (id) => {
    console.log(`person with id ${id} needs to be deleted`)
    const personToRemove = persons.find(person => person.id === id)

    // Confirm action
    const confirm = window.confirm(`Delete ${personToRemove.name}?`)
    if (confirm) {
      personService
      .remove(id)
      .then(removedPerson => {
        setPersons(persons.filter(person => person.id !== id))
      })
    }
  }

  return (
    <Container>
      <div>
        <h2>Phonebook</h2>
        <Notification message={notification} type={notificationType} />

        <Filter filterStr={filter} handleChange={handleFilterChange} />

        <h3>Add a new contact:</h3>
        <PersonForm onSubmit={addName}
          newName={newName} handleNameChange={handleNameChange}
          newNumber={newNumber} handleNumberChange={handleNumberChange}
        />

        <h3>Numbers</h3>

        <Persons persons={persons} filter={filter} handleRemovePerson={removePerson}/>
      </div>
    </Container>
  )
}

export default App